/**************************************** 
*************** HomePage ******************
****************************************/

.HomePage b {
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #008aff, 0 0 0.5em #008aff,
    0 0 0.1em #008aff, 0 10px 3px #000 !important;
    animation: textChangeColor 4s linear infinite;
}
@keyframes textChangeColor {
  0% {
    text-shadow: 0 0 50px #0f0, inset 0 0 50px #0f0;
    filter: hue-rotate(0deg);
  }

  20% {
    text-shadow: 0 0 60px #0f0, inset 0 0 60px #0f0;
  }

  40% {
    text-shadow: 0 0 40px #0f0, inset 0 0 40px #0f0;
  }

  60% {
    text-shadow: 0 0 80px #0f0, inset 0 0 80px #0f0;
  }

  80% {
    text-shadow: 0 0 100px #0f0, inset 0 0 100px #0f0;
  }

  100% {
    text-shadow: 0 0 50px #0f0, inset 0 0 50px #0f0;
    filter: hue-rotate(360deg);
  }
}
.profile-pic-container {
  border: 30px solid rgba(255, 255, 255, 0.0001);

  width: 350px;
  border-radius: 50%;
}
.wavy-circle {
  position: absolute;
  width: 460px;
  height: 460px;

  filter: url(#wavy) blur(1px);
}
.wavy-circle:before {
  content: "";
  position: absolute;
  top: 100px;
  left: 100px;
  right: 110px;
  bottom: 110px;
  border: 18px solid #fff;
  border-radius: 50%;
  box-shadow: 0 0 50px #0f0, inset 0 0 50px #0f0;
  -webkit-box-reflect: below 10px
    linear-gradient(transparent, transparent, #0002);
  animation: wavyanimate 5s linear infinite;
}
.wavy-circle:after {
  content: "";
  position: absolute;
  top: 100px;
  left: 100px;
  right: 110px;
  bottom: 110px;
  border: 18px solid #fff;
  border-radius: 50%;
  box-shadow: 0 0 10px #fff, inset 0 0 20px #fff;
}
@keyframes wavyanimate {
  0% {
    box-shadow: 0 0 50px #0f0, inset 0 0 50px #0f0;
    filter: hue-rotate(0deg);
  }

  20% {
    box-shadow: 0 0 60px #0f0, inset 0 0 60px #0f0;
  }

  40% {
    box-shadow: 0 0 40px #0f0, inset 0 0 40px #0f0;
  }

  60% {
    box-shadow: 0 0 80px #0f0, inset 0 0 80px #0f0;
  }

  80% {
    box-shadow: 0 0 100px #0f0, inset 0 0 100px #0f0;
  }

  100% {
    box-shadow: 0 0 50px #0f0, inset 0 0 50px #0f0;
    filter: hue-rotate(360deg);
  }
}
.wavy-svg {
  width: 0;
  height: 0;
}
.my-responsibility span:nth-child(1) {
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #00ff00, 0 0 0.5em #00ff00,
    0 0 0.1em #00ff00, 0 10px 3px #000 !important;
}
.my-responsibility span:nth-child(2) {
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #ffffff, 0 0 0.5em #ffffff, 0 10px 3px #000 !important;
}
.my-responsibility span:nth-child(3) {
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #ff0000, 0 0 0.5em #ff0000,
    0 0 0.1em #ff0000, 0 10px 3px #000 !important;
}
