/**************************************** 
*************** Degrees ******************
****************************************/
.degree-slider {
  width: 70%;
  box-shadow: 2px 5px 20px #099FFF;
  animation: slider-animate 5s linear infinite;
}
@keyframes slider-animate {
  0% {
    box-shadow: 0 0 50px #099FFF, inset 0 0 50px #099FFF;
    filter: hue-rotate(0deg);
  }

  20% {
    box-shadow: 0 0 60px #099FFF, inset 0 0 60px #099FFF;
  }

  40% {
    box-shadow: 0 0 40px #099FFF, inset 0 0 40px #099FFF;
  }

  60% {
    box-shadow: 0 0 80px #099FFF, inset 0 0 80px #099FFF;
  }

  80% {
    box-shadow: 0 0 100px #099FFF, inset 0 0 100px #099FFF;
  }

  100% {
    box-shadow: 0 0 50px #099FFF, inset 0 0 50px #099FFF;
    filter: hue-rotate(360deg);
  }
}
@media only screen and (max-width: 768px) {
  .degree-slider {
    width: 100%;
  }
}