/**************************************** 
*************** ContactMe ***************
****************************************/
.form-control {
  -webkit-box-shadow: 0 0px 2px 0 #008aff, 0 0px 1px 1px #008aff;
  box-shadow: 0 0px 2px 0 #008aff, 0 0px 1px 1px #008aff;
  background-color: transparent;
  border-color: transparent;
  margin-bottom: 20px;
  height: 50px;
  line-height: 50px;
  padding: 20px;
  animation: address-animate 5s linear infinite;
}
.form-control:focus {
  background-color: transparent;
  border-color: transparent;
  margin-bottom: 20px;
  height: 50px;
  line-height: 50px;
  padding: 20px;
}
.contact-message {
  -webkit-box-shadow: 0 0px 2px 0 #008aff, 0 0px 1px 1px #008aff;
  box-shadow: 0 0px 2px 0 #008aff, 0 0px 1px 1px #008aff;
  background-color: transparent;
  border-color: transparent;
  border-radius: 4px;
  margin-bottom: 20px;
  width: 100%;
  padding: 20px;
  outline: 0;
  animation: address-animate 5s linear infinite;
}
.contact-message:focus {
  border-color: transparent;
  border-radius: 4px;
  margin-bottom: 20px;
  width: 100%;
  padding: 20px;
  outline: 0;
}
.address-item {
  -webkit-box-shadow: -1rem 0 3rem #000;
  box-shadow: -1rem 0 3rem #000;
  padding: 30px;
  text-align: center;
  margin-bottom: 30px;
  border-radius: 8px;
  border: 2px solid #008aff;
  box-shadow: 2px 5px 20px #099FFF;
  animation: address-animate 4s linear infinite;
}
@keyframes address-animate {
  0% {
    box-shadow: 0 0 20px #099FFF;
    filter: hue-rotate(0deg);
  }

  20% {
    box-shadow: 0 0 20px #099FFF;
  }

  40% {
    box-shadow: 0 0 20px #099FFF;
  }

  60% {
    box-shadow: 0 0 20px #099FFF;
  }

  80% {
    box-shadow: 0 0 20px #099FFF;
  }

  100% {
    box-shadow: 0 0 20px #099FFF;
    filter: hue-rotate(360deg);
  }
}