* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  overflow-x: hidden;
}
:root {
}
/**************************************** 
********** For all Sections *************
****************************************/
.set-ele-middle {
  display: flex;
  justify-content: center;
  align-items: center;
}
ul {
  list-style: none;
}
a {
  color: #fff;
  text-decoration: none;
}
a:hover {
  color: #fff;
  text-decoration: none;
}
a:active {
  color: #fff;
  text-decoration: none;
}
a:visited {
  color: #fff;
  text-decoration: none;
}
section {
  background: url("./images/wall.jpg");
  background-repeat: repeat-y;
  background-position: center center;
  color: #fff;
  padding: 50px 0;
}
.fix-distance {
  margin-top: -90px;
}
/**************************************** 
************* Scroll Bar ****************
****************************************/
::-webkit-scrollbar {
  width: 0;
}
#scrollPath {
  position: fixed;
  top: 0;
  right: 0;
  width: 12px;
  height: 100%;
  background: rgba(255, 255, 255, 0.05);
}
#scrollBar {
  position: fixed;
  top: 0;
  right: 0;
  width: 12px;
  background: linear-gradient(to top, #008aff, #00ffe7);
  animation: animate 5s linear infinite;
}
@keyframes animate {
  0%,
  100% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(100deg);
  }
}
#scrollBar:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, #008aff, #00ffe7);
  filter: blur(10px);
}
#scrollBar:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, #008aff, #00ffe7);
  filter: blur(30px);
}
/**************************************** 
********** Neon Font Desgin **************
****************************************/
@import url(//fonts.googleapis.com/css?family=Vibur);
@font-face {
  font-family: "mexcellent";
  src: url("./Fonts/mexcellent\ 3d.ttf");
}
.neon-font2 {
  font-family: "mexcellent";
}
.neon-font {
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  user-select: none;
}
.neon-font b {
  font: 400 10vh "Vibur";
  color: #fee;
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #099fff, 0 0 0.5em #099fff,
    0 0 0.1em #099fff, 0 10px 3px #000;
}
.neon-font2 b {
  font: 400 15vh "mexcellent";
  color: #fee;
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #099fff, 0 0 0.5em #099fff,
    0 0 0.1em #099fff, 0 10px 3px #000;
}
.neon-font b span {
  animation: blink linear infinite 2s;
}
.neon-font b span:nth-of-type(2) {
  animation: blink linear infinite 3s;
}
@keyframes blink {
  78% {
    color: inherit;
    text-shadow: inherit;
  }
  79% {
    color: #333;
  }
  80% {
    text-shadow: none;
  }
  81% {
    color: inherit;
    text-shadow: inherit;
  }
  82% {
    color: #333;
    text-shadow: none;
  }
  83% {
    color: inherit;
    text-shadow: inherit;
  }
  92% {
    color: #333;
    text-shadow: none;
  }
  92.5% {
    color: inherit;
    text-shadow: inherit;
  }
}
/**************************************** 
************ Neon Button ***************
****************************************/
.NeonBtn {
  position: relative;
  display: inline-block;
  padding: 10px 35px;
  color: #099fff !important;
  background-color: rgba(255, 255, 255, 0);
  font-size: 24px;
  text-decoration: none;
  margin: 30px;
  overflow: hidden;
  -webkit-box-reflect: below 1px linear-gradiant(transparent, #0005);
  cursor: pointer;
  border: none;
}
.NeonBtn:hover {
  background: #099fff;
  color: #050801 !important;
  text-decoration: none;
  box-shadow: 0 0 5px #099fff, 0 0 25px #099fff, 0 0 50px #099fff,
    0 0 200px #099fff;
}
.NeonBtn span {
  position: absolute;
  display: block;
}
.NeonBtn span:nth-child(1),
.NeonBtn span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #099fff);
  animation: animate1 1s linear infinite;
}
@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}
.NeonBtn span:nth-child(2) {
  top: -100%;
  right: 0;
  height: 100%;
  width: 2px;
  background: linear-gradient(180deg, transparent, #099fff);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}
@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}
.NeonBtn span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #099fff);
  animation: animate3 1s linear infinite;
  animation-delay: 0.5s;
}
@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}
.NeonBtn span:nth-child(4) {
  bottom: -100%;
  left: 0;
  height: 100%;
  width: 2px;
  background: linear-gradient(360deg, transparent, #099fff);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}
@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
/**************************************** 
****** Navbar And Header (Page) *********
****************************************/
.navbar-container {
  min-height: 100vh;
  width: 100%;
  background-image: linear-gradient(to right, #434343 0%, black 100%);
  overflow-x: hidden;
  transform-style: preserve-3d;
  user-select: none;
}
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.menu {
  max-width: 72rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}
.logo {
  margin-top: 20px;
}
.logo b {
  font: 400 5vh "Vibur" !important;
}
.hamburger-menu {
  height: 4rem;
  width: 3rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.bar {
  width: 1.9rem;
  height: 1.5px;
  border-radius: 2px;
  background-color: #eee;
  transition: 0.5s;
  position: relative;
}
.bar:before,
.bar:after {
  content: "";
  position: absolute;
  width: inherit;
  height: inherit;
  background-color: #eee;
  transition: 0.5s;
}
.bar:before {
  transform: translateY(-9px);
}
.bar:after {
  transform: translateY(9px);
}
.navbar-main {
  position: relative;
  width: 100%;
  left: 0;
  z-index: 5;
  overflow: hidden;
  transform-origin: left;
  transform-style: preserve-3d;
  transition: 0.5s;
}
.navbar-header {
  min-height: 100vh;
  width: 100%;
  background: url("./images/wall.jpg");
  background-repeat: repeat;
  background-position: center center;
  position: relative;
}
.navbar-overlay {
  position: relative;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.navbar-inner {
  text-align: center;
  color: #fff;
  padding: 200px 2rem 100px 2rem;
  transition: all 0.5s;
}
@media only screen and (max-width: 420px) {
  .profile-pic-container {
    height: 280px;
    width: 280px;
  }
}
.navbar-container.active .bar {
  transform: rotate(360deg);
  background-color: transparent;
}
.navbar-container.active .bar:before {
  transform: translateY(0) rotate(45deg);
}
.navbar-container.active .bar:after {
  transform: translateY(0) rotate(-45deg);
}
.navbar-container.active .navbar-main {
  animation: navbar-main-animation 0.5s ease;
  cursor: pointer;
  transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
}
@keyframes navbar-main-animation {
  from {
    transform: translate(0);
  }

  to {
    transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
  }
}
.navbar-links {
  position: absolute;
  padding: 0 75px;
  width: 30%;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar-links a {
  text-decoration: none;
  color: #eee;
  padding: 0.7rem 0;
  display: inline-block;
  font-size: 1rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: 0.3s;
  opacity: 0;
  transform: translateY(10px);
  animation: hide 0.5s forwards ease;
}
.navbar-links a:hover {
  color: #fff;
}
.navbar-container.active .navbar-links a {
  animation: appear 0.5s forwards ease var(--i);
}
@keyframes appear {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes hide {
  from {
    opacity: 1;
    transform: translateY(0px);
  }
  to {
    opacity: 0;
    transform: translateY(10px);
  }
}
.shadow {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  transform-style: preserve-3d;
  transform-origin: left;
  transition: 0.5s;
  background-color: white;
}
.shadow.one {
  z-index: -1;
  opacity: 0.15;
}
.shadow.two {
  z-index: -2;
  opacity: 0.1;
}
.navbar-container.active .shadow.one {
  animation: shadow-one 0.6s ease-out;
  transform: perspective(1300px) rotateY(20deg) translateZ(215px) scale(0.5);
}
@keyframes shadow-one {
  0% {
    transform: translate(0);
  }

  5% {
    transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
  }

  100% {
    transform: perspective(1300px) rotateY(20deg) translateZ(215px) scale(0.5);
  }
}
.navbar-container.active .shadow.two {
  animation: shadow-two 0.6s ease-out;
  transform: perspective(1300px) rotateY(20deg) translateZ(120px) scale(0.5);
}
@keyframes shadow-two {
  0% {
    transform: translate(0);
  }

  20% {
    transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
  }

  100% {
    transform: perspective(1300px) rotateY(20deg) translateZ(120px) scale(0.5);
  }
}
.navbar-container.active .navbar-main:hover + .shadow.one {
  transform: perspective(1300px) rotateY(20deg) translateZ(230px) scale(0.5);
}
.navbar-container.active .navbar-main:hover {
  transform: perspective(1300px) rotateY(20deg) translateZ(340px) scale(0.5);
}
.social-media li {
  display: inline;
  margin: 0 15px;
}
.pre-loader {
  position: fixed;
  min-height: 100vh;
  width: 100%;
  z-index: 2000;
  background: rgba(0, 0, 0, 0.9);
}

.loader {
  display: flex;
  align-items: flex-end;
  transform: translateY(-35px);
}

.loader span {
  width: 20px;
  height: 20px;
  background: #111;
  margin: 0 10px;
  height: calc(20px * var(--i));
  display: flex;
  justify-content: center;
  align-items: flex-end;
  animation: loader-animate 2s linear infinite;
  animation-delay: calc(0.25s * var(--i));
}

@keyframes loader-animate {
  0% {
    background: #fff;
    filter: blur(2px);
    box-shadow: 0 0 10px #00b3ff, 0 0 20px #00b3ff, 0 0 40px #00b3ff,
      0 0 80px #00b3ff, 0 0 120px #00b3ff, 0 0 200px #00b3ff, 0 0 300px #00b3ff,
      0 0 400px #00b3ff;
  }

  25%,
  75% {
    background: #111;
    filter: blur(0px);
    box-shadow: none;
  }

  100% {
    background: #fff !important;
    filter: blur(2px);
    box-shadow: 0 0 10px #00b3ff, 0 0 20px #00b3ff, 0 0 40px #00b3ff,
      0 0 80px #00b3ff, 0 0 120px #00b3ff, 0 0 200px #00b3ff, 0 0 300px #00b3ff,
      0 0 400px #00b3ff;
  }
}
.loader span i {
  display: block;
  font-style: normal;
  color: #fff;
  font-size: 50px;
  font-weight: bold;
  transform: translateY(70px);
  animation: loader-animate2 2s linear infinite;
  animation-delay: calc(0.25s * var(--i));
}
@keyframes loader-animate2 {
  0% {
    color: #fff;
    filter: blur(2px);
    text-shadow: 0 0 10px #00b3ff, 0 0 20px #00b3ff, 0 0 40px #00b3ff,
      0 0 80px #00b3ff, 0 0 120px #00b3ff, 0 0 200px #00b3ff, 0 0 300px #00b3ff,
      0 0 400px #00b3ff;
  }
  25%,
  75% {
    color: #111;
    filter: blur(0px);
    text-shadow: none;
  }
  100% {
    color: #fff;
    filter: blur(2px);
    text-shadow: 0 0 10px #00b3ff, 0 0 20px #00b3ff, 0 0 40px #00b3ff,
      0 0 80px #00b3ff, 0 0 120px #00b3ff, 0 0 200px #00b3ff, 0 0 300px #00b3ff,
      0 0 400px #00b3ff;
  }
}
