@keyframes noise {
  0% {
    background-position-y: 0px;
  }
  100% {
    background-position-y: -420px;
  }
}
.Error404 {
  overflow: hidden;
  margin: 0px;
  background: rgb(31, 31, 31);
  height: 100vh;
  box-shadow: 0px 0px 122px 25px black inset;
  background-image: url("https://i.imgur.com/eg7VIun.jpg");
  background-repeat: repeat;
  animation: noise 230ms steps(6) infinite;
  user-select: none;
}
.ErrorContainer {
  min-height: 100vh;
}
.glitch-wrapper {
  width: 100vw;
  text-align: center;
  animation: glitch 2s linear infinite;
  margin-bottom: 110px;
}
.glitch-text {
  font-family: monospace;
  font-weight: bold;
}
.glitch-text::before {
  content: "ERROR 404";
  color: black;
  text-shadow: 0px 0px 5px 10px black !important;
  -webkit-filter: blur(7px);
  -moz-filter: blur(7px);
  -o-filter: blur(7x);
  -ms-filter: blur(7px);
  filter: blur(7px);
  display: block;
  position: absolute;
  z-index: -1;
  font-size: 132px;
  left: 0px;
  right: 0px;
  margin-top: -30px;
  opacity: 0.5;
}
.glitch-text {
  position: relative;
  z-index: 2;
  opacity: 0.9;
  margin: 0 auto;
  text-shadow: 0px 0px 3px white;
  animation: glitch 3s infinite;
  cursor: pointer;
  font-size: 52px;
  color: white;
}
/* JUST RANDOM VALUES HERE! */
@keyframes glitch {
  3% {
    text-shadow: 7px 7px 0px red;
  }
  6% {
    text-shadow: -9px 4px 0px blue;
    transform: translate(4px, -10px) skewX(3240deg);
  }
  7% {
    text-shadow: 3px -4px 0px green;
  }
  9% {
    text-shadow: 0px 0px 3px white;
    transform: translate(0px, 0px) skewX(0deg);
  }
  2%,
  54% {
    transform: translateX(0px) skew(0deg);
  }
  55% {
    transform: translate(-2px, 6px) skew(-5530deg);
  }
  56% {
    text-shadow: 0px 0px 3px white;
    transform: translate(0px, 0px) skew(0deg);
  }
  57% {
    text-shadow: 7px 7px 0px red;
    transform: translate(4px, -10px) skew(-70deg);
  }
  58% {
    text-shadow: 0px 0px 3px white;
    transform: translate(0px, 0px) skew(0deg);
  }
  62% {
    text-shadow: 3px -4px 0px green;
    transform: translate(0px, 20px) skew(0deg);
  }
  63% {
    text-shadow: 0px 0px 3px white;
    transform: translate(4px, -2px) skew(0deg);
  }
  64% {
    transform: translate(1px, 3px) skew(-230deg);
  }
  65% {
    transform: translate(-7px, 2px) skew(120deg);
  }
  66% {
    transform: translate(0px, 0px) skew(0deg);
  }
}

.go-to-home a {
  background: rgba(9,159,255,0.6);
  border-radius: 10px;
  color: #050801 !important;
}
