/**************************************** 
*************** AboutMe *****************
****************************************/
.about-me2 {
  margin-top: 130px;
}
.guitar {
  transform: rotate(-45deg);
}
.about-list li {
  display: inline-block;
  margin: 0px 6px;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 4px 0px;
  display: inline-block;
}
.about-list li span {
  padding: 4px 10px;
  border-radius: 4px;
  color: #fff;
  border: 1px solid #ff0a58;
  box-shadow: 3px 3px 12px #ff0a44;
}
.about-para p {
  width: 75%;
  font-size: 23px;
  text-align: left;
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #008aff, 0 0 0.5em #008aff,
  0 0 0.1em #008aff, 0 10px 3px #000 !important;
  animation: textChangeColor 4s linear infinite;
}
@keyframes textChangeColor {
  0% {
    text-shadow: 0 0 50px #0f0, inset 0 0 50px #0f0;
    filter: hue-rotate(0deg);
  }

  20% {
    text-shadow: 0 0 60px #0f0, inset 0 0 60px #0f0;
  }

  40% {
    text-shadow: 0 0 40px #0f0, inset 0 0 40px #0f0;
  }

  60% {
    text-shadow: 0 0 80px #0f0, inset 0 0 80px #0f0;
  }

  80% {
    text-shadow: 0 0 100px #0f0, inset 0 0 100px #0f0;
  }

  100% {
    text-shadow: 0 0 50px #0f0, inset 0 0 50px #0f0;
    filter: hue-rotate(360deg);
  }
}
.card-container {
  border: 2px solid #c600f5;
  height: 350px;
  padding: 20px;
  margin: 15px 0;
  border-radius: 4px;
  box-shadow: 2px 5px 20px #c600f5;
  background-color: rgba(0, 0, 0, 0.35);
}
/**************************************** 
************* Neon Card *****************
*****************************************/
.neon-card-containter {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  flex-wrap: wrap;
}
.neon-card-containter .neon-card-box {
  position: relative;
  width: 320px;
  height: 400px;
  color: #fff;
  background: #111;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 30px;
  transition: 0.5s;
}
.neon-card-containter .neon-card-box:hover {
  transform: translateY(-20px);
}
.neon-card-containter .neon-card-box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #ffbc00, #ff0058);
}
.neon-card-containter .neon-card-box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #ffbc00, #ff0058);
  filter: blur(30px);
}
.neon-card-containter .neon-card-box span {
  position: absolute;
  top: 6px;
  left: 6px;
  right: 6px;
  bottom: 6px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
}
.neon-card-containter .neon-card-box span::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  pointer-events: none;
}
.neon-card-containter .neon-card-box .neon-card-content {
  position: relative;
  z-index: 10;
  padding: 20px 40px;
}
.neon-card-containter .neon-card-box .neon-card-content h2 {
  font-size: 2em;
  color: #fff;
  margin-bottom: 10px;
}
.neon-card-containter .neon-card-box .neon-card-content p {
  font-size: 1.1em;
  color: #fff;
  margin-bottom: 10px;
  line-height: 1.4em;
}
.neon-card-containter .neon-card-box .neon-card-content a {
  display: inline-block;
  font-size: 1.1em;
  color: #111;
  background: #fff;
  padding: 10px;
  text-decoration: none;
  font-weight: 700;
}
.neon-card-containter .neon-card-box:nth-child(2)::before,
.neon-card-containter .neon-card-box:nth-child(2)::after {
  background: linear-gradient(45deg, #03a9f4, #ff0a58);
}
.neon-card-containter .neon-card-box:nth-child(3)::before,
.neon-card-containter .neon-card-box:nth-child(3)::after {
  background: linear-gradient(45deg, #4dff03, #00d0ff);
}