/**************************************** 
************* Scroll Bar ****************
****************************************/
::-webkit-scrollbar {
  width: 0;
}
#scrollPath {
  position: fixed;
  top: 0;
  right: 0;
  width: 12px;
  height: 100%;
  background: rgba(255, 255, 255, 0.05);
}
#scrollBar {
  position: fixed;
  top: 0;
  right: 0;
  width: 12px;
  background: linear-gradient(to top, #008aff, #00ffe7);
  animation: animate 5s linear infinite;
}
@keyframes animate {
  0%,
  100% {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  50% {
    -webkit-filter: hue-rotate(100deg);
            filter: hue-rotate(100deg);
  }
}
#scrollBar:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, #008aff, #00ffe7);
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
#scrollBar:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, #008aff, #00ffe7);
  -webkit-filter: blur(30px);
          filter: blur(30px);
}
