/**************************************** 
****** Navbar And Header (Page) *********
****************************************/
.navbar-container {
  min-height: 100vh;
  width: 100%;
  background-image: linear-gradient(to right, #434343 0%, black 100%);
  overflow-x: hidden;
  transform-style: preserve-3d;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.menu {
  max-width: 72rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}
.logo {
  margin-top: 20px;
}
.logo b {
  font: 400 5vh "Vibur" !important;
}
.hamburger-menu {
  height: 4rem;
  width: 3rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.bar {
  width: 1.9rem;
  height: 1.5px;
  border-radius: 2px;
  background-color: #eee;
  transition: 0.5s;
  position: relative;
}
.bar:before,
.bar:after {
  content: "";
  position: absolute;
  width: inherit;
  height: inherit;
  background-color: #eee;
  transition: 0.5s;
}
.bar:before {
  transform: translateY(-9px);
}
.bar:after {
  transform: translateY(9px);
}
.navbar-main {
  position: relative;
  width: 100%;
  left: 0;
  z-index: 5;
  overflow: hidden;
  transform-origin: left;
  transform-style: preserve-3d;
  transition: 0.5s;
}
.navbar-header {
  min-height: 100vh;
  width: 100%;
  background: url(../../static/media/wall.4776ff17.jpg);
  background-repeat: repeat;
  background-position: center center;
  position: relative;
}
.navbar-overlay {
  position: relative;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.navbar-inner {
  text-align: center;
  color: #fff;
  padding: 200px 2rem 100px 2rem;
  transition: all 0.5s;
}
@media only screen and (max-width: 420px) {
  .profile-pic-container {
    height: 280px;
    width: 280px;
  }
}
.navbar-container.active .bar {
  transform: rotate(360deg);
  background-color: transparent;
}
.navbar-container.active .bar:before {
  transform: translateY(0) rotate(45deg);
}
.navbar-container.active .bar:after {
  transform: translateY(0) rotate(-45deg);
}
.navbar-container.active .navbar-main {
  animation: navbar-main-animation 0.5s ease;
  cursor: pointer;
  transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
}
@keyframes navbar-main-animation {
  from {
    transform: translate(0);
  }

  to {
    transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
  }
}
.navbar-links {
  position: absolute;
  padding: 0 75px;
  width: 30%;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar-links a {
  text-decoration: none;
  color: #eee;
  padding: 0.7rem 0;
  display: inline-block;
  font-size: 1rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: 0.3s;
  opacity: 0;
  transform: translateY(10px);
  animation: hide 0.5s forwards ease;
}
.navbar-links a:hover {
  color: #fff;
}
.navbar-container.active .navbar-links a {
  animation: appear 0.5s forwards ease var(--i);
}
@keyframes appear {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes hide {
  from {
    opacity: 1;
    transform: translateY(0px);
  }
  to {
    opacity: 0;
    transform: translateY(10px);
  }
}
.shadow {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  transform-style: preserve-3d;
  transform-origin: left;
  transition: 0.5s;
  background-color: white;
}
.shadow.one {
  z-index: -1;
  opacity: 0.15;
}
.shadow.two {
  z-index: -2;
  opacity: 0.1;
}
.navbar-container.active .shadow.one {
  animation: shadow-one 0.6s ease-out;
  transform: perspective(1300px) rotateY(20deg) translateZ(215px) scale(0.5);
}
@keyframes shadow-one {
  0% {
    transform: translate(0);
  }

  5% {
    transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
  }

  100% {
    transform: perspective(1300px) rotateY(20deg) translateZ(215px) scale(0.5);
  }
}
.navbar-container.active .shadow.two {
  animation: shadow-two 0.6s ease-out;
  transform: perspective(1300px) rotateY(20deg) translateZ(120px) scale(0.5);
}
@keyframes shadow-two {
  0% {
    transform: translate(0);
  }

  20% {
    transform: perspective(1300px) rotateY(20deg) translateZ(310px) scale(0.5);
  }

  100% {
    transform: perspective(1300px) rotateY(20deg) translateZ(120px) scale(0.5);
  }
}
.navbar-container.active .navbar-main:hover + .shadow.one {
  transform: perspective(1300px) rotateY(20deg) translateZ(230px) scale(0.5);
}
.navbar-container.active .navbar-main:hover {
  transform: perspective(1300px) rotateY(20deg) translateZ(340px) scale(0.5);
}
