/**************************************** 
*************** Skills ******************
****************************************/
.skills b,
.skills h2 {
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #099FFF, 0 0 0.5em #099FFF,
    0 0 0.1em #099FFF, 0 10px 3px #000 !important;
}
.progress {
  margin-bottom: 20px;
  width: 80%;
}
.progress-header {
  width: 80%;
}
.progress-meter {
  box-sizing: content-box;
  height: 14px;
  position: relative;
  margin: 0 0 20px 0;
  background: rgba(0, 0, 0, 0.85);
  border-radius: 12px;
  padding: 10px;
  box-shadow: inset 0 -1px 1px rgba(255, 255, 255, 0.3);
}
.progress-meter > span {
  display: block;
  height: 100%;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: rgb(43, 194, 83);
  background-image: linear-gradient(
    center bottom,
    rgb(43, 194, 83) 37%,
    rgb(84, 240, 84) 69%
  );
  box-shadow: 0 0 60px #0f0, inset 0 0 60px #0f0;
  position: relative;
  overflow: hidden;
  
}
.progress-meter > span:after,
.animate > span > span {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(
    -45deg,
    rgba(255, 255, 255, 0.2) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.2) 75%,
    transparent 75%,
    transparent
  );
  z-index: 1;
  background-size: 50px 50px;
  animation: move 2s linear infinite;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  overflow: hidden;
}
.animate > span:after {
  display: none;
}
@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 50px 50px;
  }
}
.nostripes > span > span,
.nostripes > span::after {
  background-image: none;
}
.progress-wrap {
  width: 80%;
  height: 50%;
}
.humberger-image img {
  margin-top: -50px;
}
@media only screen and (max-width: 768px) {
  .humberger-image img {
    width: 80%;
    margin-top: 0;
  }
}