/**************************************** 
*************** Expriences ***************
****************************************/
.epxs b,
.epxs h2 {
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #099FFF, 0 0 0.5em #099FFF,
    0 0 0.1em #099FFF, 0 10px 3px #000 !important;
}
.edu-item {
  width: 70%;
  margin-bottom: 30px;
  padding: 26px 30px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.05);
  border: 2px solid #099FFF;
  box-shadow: 2px 5px 20px #099FFF;
  animation: exprience-animate 5s linear infinite;
}
@keyframes exprience-animate {
  0% {
    box-shadow: 0 0 20px #099FFF;
    filter: hue-rotate(0deg);
  }

  20% {
    box-shadow: 0 0 20px #099FFF;
  }

  40% {
    box-shadow: 0 0 20px #099FFF;
  }

  60% {
    box-shadow: 0 0 20px #099FFF;
  }

  80% {
    box-shadow: 0 0 20px #099FFF;
  }

  100% {
    box-shadow: 0 0 20px #099FFF;
    filter: hue-rotate(360deg);
  }
}
.edu-item h4 {
  font-size: 20px;
  line-height: 30px;
  font-weight: 600;
}
.edu-item h4 a {
  font-style: italic;
  margin-left: 4px;
  color: #099FFF;
}
.edu-item .mh-eduyear {
  margin: 10px 0;
  color: #099FFF;
  display: inline-block;
}
.edu-item p {
  color: #099FFF;
  font-size: 14px;
  line-height: 23px;
  display: inline-block;
  margin-bottom: 0;
}
